        <h2 class="Portfolio__content-title">Эссе</h2>
        <p class="Portfolio__content-text">Интерес к истории пробуждался постепенно, как аппетит у человека (который, как известно, «приходит во время еды»). Началось всё со школьных уроков, с рассказов учителей о делах, давно минувших: о преданности и предательствах, о подвигах и трусости. Как черное и белое, человеческие качества сходились на полотне истории и образовывали там небывалое переплетение красок. По-настоящему увлек меня в мир истории мой отец, кандидат исторических наук, доцент В.Н.Трухачев. Итак, историк – это исследователь, который должен отделить пшеницу от плевел, а если это невозможно, то хотя бы попытаться.
Учась на историческом факультете ТГПУ, я вернулся в свою школу. Об этом я мечтал с последнего звонка. Но в 2001 году я стал преподавателем в Ташкентском Политехническом профессиональном колледже. Работа преподавателя оказалась не такой простой, как мне представлялось в школьные годы. Слово работа здесь не совсем подходит. Служение… Преподаватель – в некотором роде священнослужитель, он поставлен обществом выполнять важнейшую функцию – передавать подрастающему поколению накопленный человечеством опыт. От того, как преподаватель относится к своему занятию, зависит очень многое. В 2018 году после того, как я попал под сокращение в колледже, мне вновь пришлось вернуться работать в школу.
Сердце каждого ученика открывается по-разному. Надо только найти ключик. С группой надо подружиться, видеть в учащихся не объект воспитательной деятельности, а товарищей. Преподаватель, по моему твердому убеждению, – это тот человек, в котором учащийся может обрести друга и помощника, советника. При этом, безусловно, необходимо держать определенную дистанцию, иначе учебный процесс остановится вовсе. 
Задача педагога – дать путь к познанию для всех и каждого.
Использование передовых информационных технологий становится все более востребованным. В условиях оскудения интереса к книге все большее значение приобретают мультимедийные программы общеобразовательной направленности.
Учащимся должно быть интересно учиться. Поэтому хорошо, когда преподаватель включает в учебный процесс элементы игры и состязательности. Также росту интереса к истории способствуют различные мероприятия во внеурочное время: викторины, конкурсы, посещение музеев, представления, просмотры исторических фильмов.
Мне очень близка мысль, сформулированная давно, но от этого не ставшая менее актуальной: основная цель деятельности преподавателя – не «научить», а способствовать формированию у детей навыков самостоятельного мышления и поиска. Именно такие граждане нужны нашей стране: творческие, активные, смелые. И если в этом будущем будет частичка моих творческих усилий, я буду считать свою деятельность не напрасной.
</p>

<span class="Portfolio__content-titext">Занимаемая должность: </span>
<p class="Portfolio__content-text">Цель: мониторинг профессионального роста, отражающий уровень компетентности и конкурентоспособности;
Задачи: 1 выявление достижений в работе и их влияние на результаты деятельности; 2 предоставление возможности для самореализации и развития творческого потенциала; 3 представление, популяризация и распространение опыта работы; 4 повышение престижа профессии преподавателя; 5 развитие рефлексии и критического мышления ; 6 систематизация накопленного материала.
</p>
<span class="Portfolio__content-titext">Педагогическое кредо:</span>
<p class="Portfolio__content-text">
    «Жить – вот ремесло, которому я хочу учить воспитанника. Выходя из моих рук, он будет не судьей, не солдатом, не священником: он будет, прежде всего, человеком: всем, чем должен быть человек, он сумеет быть в случае необходимости, так же хорош, как и всякий другой, и как бы судьба не перемещала его с места на место, он всегда будет на своем месте» Ж.Ж.Руссо
Эти слова Руссо запали мне в душу еще во время учебы в университете. И до сих пор я не перестаю думать, а как же действительно сделать так, чтобы научить ребенка жить, как помочь ему стать человеком… как его научить…? Каким должен быть тот Учитель, который пытается это сделать? Наверное, на эти вопросы никогда не удастся ответить со 100 % уверенностью…но все же…Я считаю, что Учитель должен обладать умением найти правильный подход к каждому ребенку, чтобы зажегся в нем огонек познания, чтобы появилось желание учиться. Главное — понимать ребёнка. Открыть ему свое сердце, впустить и принять его таким какой он есть. Ведь каждый из них, прежде всего, личность, со своим мировоззрением, миропониманием. А значит, главное предназначение человека, решившего посвятить себя работе с детьми, развить, обогатить эту личность. Необходимо создать условия для творческого развития ребенка, не указать проторенный путь познания, а помочь найти свою, пусть даже узенькую и труднопроходимую тропу; не тянуть за руку на гору, находясь на ее вершине, а помочь преодолеть ее, идя рядом, вовремя подставив плечо. Как этого достичь? На самом деле все просто: учителю нужно самому быть яркой личностью. Ведь как писал К. Д. Ушинский «только личность может воспитать личность»…

</p>
<span class="Portfolio__content-titext">Педагогические задачи:</span>
<p class="Portfolio__content-text">
    В процессе моего становления в роли преподавателя я определил для себя следующие педагогические задачи, которые и стараюсь решать каждый день: 1. Научить учащихся быть независимыми. Чем больше мы для них делаем, тем меньше они учатся делать для себя сами, ведь как гласит известная поговорка: «Голодному человеку дайте не жареную рыбу, а невод»;
2. Необходимо воодушевлять учащихся максимально быть самими собой, ободрять каждого, чтобы он гордился своими достижениями; 
3. Создать условия для выбора каждым учеником своего собственного образовательного маршрута, развития учащегося в соответствии с его индивидуальными особенностями; 
4. Убедить ученика в том, что он кладезь возможностей, заставить его поверить в себя, в свои силы, предоставить возможность получать удовольствие и радость от результатов своего труда; 
5. Моя практическая задача – привести роман ученика с историей к счастливому продолжению;
6.Подготовить выпускников школ, обладающих глубокими знаниями, широким кругозором и умением сориентироваться на самостоятельное трудоустройство.
</p>
<span class="Portfolio__content-titext">Использование современных образовательных технологий и методик в образовательном процессе:</span>
<p class="Portfolio__content-text">
    В основе моей методической системы лежит использование технологии проблемного обучения, критического мышления, проектной технологии и ИКТ в рамках компетентностного и деятельностного подхода в обучении. В соответствии с деятельностным подходом основным элементом работы учащихся является решение задач, т.е., освоение деятельности: учебно - исследовательской, поисково-конструкторской, творческой и др. Параллельно с этим учащийся сможет сформировать свою систему ценностей, поддерживаемую социумом. Из пассивного потребителя знаний учащийся становится активным субъектом образовательной деятельности. Обсуждение и решение проблемных вопросов расширяет базу проектной и исследовательской деятельности учащихся в рамках внеурочной деятельности по предмету и работы с одаренными детьми. Это делает возможным результативное участие в олимпиадах, конкурсах, конференциях. Свою роль также вижу в том, чтобы способствовать социализации учащихся, развивать у них социальные компетенции, необходимые для успешной адаптации в социуме, самоутверждения и самореализации в жизни. В целях усиления практикоориентированной составляющей образовательного процесса выбираю наиболее соответствующие ей формы организации обучения, которые обеспечивают приоритет самостоятельной познавательной деятельности учащихся, требуют привлечения и осмысления личного социального опыта, побуждают высказывать собственное мнение, давать личностную оценку, создают условия для свободного творческого самовыражения. Не менее 50% учебного времени отвожу на самостоятельную работу учащихся, позволяющую им приобрести опыт познавательной и практической деятельности.
Использование данной методической системы позволяет: научить учащихся ориентироваться в потоке информации, искать, анализировать, переосмысливать; развивать субъектность, индивидуальность, личностный потенциал учащегося, творчество, самостоятельность в решении проблемных ситуаций; формировать у учащихся ключевые компетенции, которые помогут ему в дальнейшем образовании и успешной социализации в будущей взрослой жизни. Созданная методическая система помогает достичь более высоких результатов, чем традиционные технологии, позволяет научить учащихся проектировать собственную траекторию движения при решении того или иного вопроса; создает условия для повышения мотивации и интенсификации общения учителя и ученика. Этому способствует применение проблемной дискуссии, решение проблемных задач, проектная и исследовательская деятельность, возможности ИКТ.  
Результатом является формирование ключевых компетенций у учащихся, таких универсальных способностей и умений, которые позволяют человеку успешно социализироваться, достичь желаемых результатов в личной и профессиональной жизни.

</p>
<span class="Portfolio__content-titext">Внеурочная деятельность по преподаваемому предмету:</span>
<p class="Portfolio__content-text">
    Один из путей решения этой проблемы – внеурочная внеклассная работа, которая является неотъемлемой составляющей учебно-воспитательного процесса. Если информация, полученная на уроке, заинтересовала учащихся, то у них возникает желание изучать историю и право и после звонка. Внеурочная работа по истории и праву – это организация преподавателем различных видов деятельности учащихся после уроков, обеспечивающих необходимые условия для овладения ими навыками и умениями теоретической и практической работы по более глубокому усвоению и активному восприятию исторического опыта и окружающей действительности. . Внеклассная работа предполагает максимум самодеятельности и активности обучающихся, но принцип самодеятельности и активности абсолютно не исключает руководящей роли преподавателя. Именно он направляет инициативу и интерес учащихся, всемерно развивает их творчество, прививает им навыки самостоятельной работы, способствует активизации познавательной деятельности. Внеклассная работа может протекать в разных формах. В своей педагогической деятельности я использую: кружковую работу; индивидуальную форму; организацию различных мероприятий по предметам; мои учащиеся принимают участие в олимпиадах, викторинах, конкурсах и конференциях.
</p>
